<template>
  <div class="detail">
    <div class="my-bg">
      <van-nav-bar>
        <template #title>
          <div class="classify-title">书籍详情</div>
        </template>
        <template #right>
          <div class="right-icon" @click="goSearch">
            <img
              src="../assets/images/12-white.png"
              alt
              class="auto-img img-height"
            />
          </div>
        </template>
        <template #left>
          <div class="right-icon">
            <img
              src="../assets/images/left-white.png"
              alt
              class="auto-img"
              @click="goback"
            />
          </div>
        </template>
      </van-nav-bar>
      <div class="rank">
        <div class="rank-list">
          <div class="rank-list-left">
            <img
              :src="detailList.cover"
              alt
              class="auto-img rank-list-left-img"
            />
          </div>
          <div class="rank-list-right">
            <div class="rank-name">{{ detailList.name }}</div>
            <div class="rank-rate" v-show="detailList.cover">
              <el-rate
                v-model="value"
                disabled
                show-score
                text-color="#fff"
                score-template="{value}"
                disabled-void-color="#fff"
              ></el-rate>
            </div>
            <div class="rank-author">{{ detailList.author }}</div>
            <div class="rank-logo" v-show="detailList.cover">
              <span class="logo-tag" v-show="detailList.tag">{{
                detailList.tag
              }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-if="detailList.cover">
      <div class="detail-use">
        <div class="use-list">
          <div class="use-list-icon">
            <img src="../assets/images/pinglun.png" alt class="auto-img" />
          </div>
          <div class="use-list-text">3294</div>
        </div>
        <div class="use-list">
          <div class="use-list-icon">
            <img src="../assets/images/xiazai.png" alt class="auto-img" />
          </div>
          <div class="use-list-text">2469</div>
        </div>
        <div class="use-list">
          <div class="use-list-icon">
            <img src="../assets/images/fenxiang.png" alt class="auto-img" />
          </div>
          <div class="use-list-text">4778</div>
        </div>
      </div>
      <div class="subfield clearfix">
        <div class="line fl"></div>
        <div class="subfield-title fl">简介</div>
        <div class="more fr" @click="clickmore">更多</div>
      </div>
      <div class="subfield-info" v-if="isShow">
        {{ detailList.introduce }}
      </div>
      <div class="subfield-info-more" v-else>
        {{ detailList.introduce }}
      </div>
      <div class="subfield clearfix">
        <div class="line fl"></div>
        <div class="subfield-title fl">目录</div>
        <div class="more fr" @click="clickodd">更多</div>
      </div>
      <div class="catalogue clearfix">
        <div class="catalogue-list fl">
          <div class="order" v-if="isshow">
            <div
              class="catalogue-list-text"
              v-for="(item, index) in catalogueThree"
              :key="index"
              @click="reading(index)"
            >
              {{ item.num }}
            </div>
          </div>
          <div class="orderOver" v-else>
            <el-backtop></el-backtop>
            <div
              class="catalogue-list-text"
              v-for="(item, index) in catalogue"
              :key="index"
              @click="reading(index)"
            >
              {{ item.num }}
            </div>
          </div>
          <div class="renew">更新至：{{ detailList.latest }}</div>
        </div>
      </div>
      <div class="total-dir" @click="clickdir" v-if="isDir">
        全部目录({{ catalogue.length }})
      </div>
      <div class="total-dir" @click="clickdir" v-else>
        全部目录({{ catalogue.length }})
      </div>
      <div class="detail-foot clearfix">
        <div class="read fl">
          <div @click="reading(0)">立即阅读</div>
        </div>
        <div class="bookrack fr" @click="addBook">
          <div v-show="!isjoin">加入书架</div>
          <div v-show="isjoin">移除书架</div>
        </div>
      </div>
    </div>
    <div class="no" v-else>暂无此书内容，无版权</div>
  </div>
</template>

<script>
import "../assets/less/detail.less";
export default {
  data() {
    return {
      // 评分
      value: 3.4,
      // 简介
      isShow: true,
      // 详情展示列表
      detailList: {},
      // 标题
      title: "",
      //目录
      catalogue: "",
      //目录前三章
      catalogueThree: "",
      // 目录显示
      isshow: true,
      // 收起目录
      isDir: true,
      //url
      urlbook: "",
      //从搜索页进入
      tag: "",
      //从更多页进入
      logo: "",
      //从分类页进入
      find: "",
      //更多url
      mhlb: "",
      //加入书架与否
      isjoin: false,
      //加入书架的总数据
      addbookrack: "",
      // 判断能否加入书架
      noLook: "",
    };
  },
  created() {
    // 获取数据
    this.getData();
    
  },
  mounted() {
    //  获取加入或者移除标识
    let sigh = JSON.parse(localStorage.getItem("add"));
    
    if (!sigh) {
      return;
    }
    if (
      sigh.author == this.detailList.author &&
      sigh.name == this.detailList.name
    ) {
      this.isjoin = true;
    }
  },
  methods: {
    //获取数据
    getData() {
      this.urlbook = this.$route.query.bookurl;
      //搜索标识
      this.tag = this.$route.query.tag;
      //更多标识
      this.logo = this.$route.query.logo;
      //分类标识
      this.find = this.$route.query.find;
      
      //更多url
      this.mhlb = this.$route.query.mhlb;
      //标题
      this.title = this.$route.query.title;
      //能否加入书架
      this.noLook = this.$route.query.nolook;
      
      

      this.axios({
        mrthods: "GET",
        url: "http://47.114.147.221",
        params: {
          mhurl1: this.urlbook,
        },
      })
        .then((res) => {
          
          this.addbookrack = res.data;
          if (!res.data.data.cover) {
            return;
          }
          this.detailList = res.data.data;

          let urlbookArr = this.urlbook.split("/");
          // 倒序
          if (urlbookArr[0] == "mh123" && urlbookArr[1] == "comic") {
            this.catalogue = res.data.list.reverse();
            this.catalogueThree = this.catalogue.slice(0, 3);
          }
          this.catalogue = res.data.list;
          this.catalogueThree = this.catalogue.slice(0, 3);
          //获取加入或移除标识
          let sigh = JSON.parse(localStorage.getItem("Bookrack"));
          let sighIs = sigh.some((v) => {
            return (
              v.data.author == this.detailList.author &&
              v.data.name == this.detailList.name
            );
          });
          if (sighIs) {
            this.isjoin = true;
          }
        })
        .catch(() => {});
    },
    // 简介显示
    clickmore() {
      this.isShow = !this.isShow;
    },
    // 目录显示
    clickodd() {
      this.isshow = !this.isshow;
    },
    // 目录收起
    clickdir() {
      this.isshow = !this.isshow;
      this.isDir = !this.isDir;
    },
    // 后退
    goback() {
      
      //退回搜索页
      if (this.tag) {
        this.$router.push({
          name: "Search",
        });
        return;
      }
      
      //返回更多页面
      if (this.logo) {
        
        this.$router.push({
          name: "More",
          query: { title: this.title, mhlb: this.mhlb, find: this.find },
        });
        return;
      }
      this.$router.push({
        name: "Home",
      });
    },
    // 搜索
    goSearch() {
      this.$router.push({ name: "Search" });
    },
    // 阅读
    reading(Tindex) {
      // 判断是否有内容数据
      if (this.catalogue.length <= 0) {
        this.$toast("暂无数据，无法阅读，去看看其他漫画吧");
        return;
      }
      
      this.$router.push({
        name: "Read",
        query: {
          urlbook: this.urlbook,
          index: Tindex,
          logo: this.logo,
          title: this.title,
          mhlb: this.mhlb,
          find: this.find,
          tag:this.tag
        },
      });
    },
    //加入书架
    addBook() {
      // 判断是否可以加入书架
      if (this.catalogue.length <= 0) {
        this.$toast("无法加入书架");
        return;
      }
      if (this.noLook == "No") {
        this.$toast("无法加入书架");
        return;
      }
      this.isjoin = !this.isjoin;

      if (this.isjoin) {
        // 第一次加入书架
        let bookrack = JSON.parse(localStorage.getItem("Bookrack"));
        if (!bookrack) {
          
          let books = [];
          localStorage.setItem("Bookrack", JSON.stringify(books));
          let bookrack = JSON.parse(localStorage.getItem("Bookrack"));
          bookrack.unshift(this.addbookrack);
          localStorage.setItem("Bookrack", JSON.stringify(bookrack));
          return;
        }
        let bookracks = JSON.parse(localStorage.getItem("Bookrack"));
        //加入书架
        bookracks.unshift(this.addbookrack);
        localStorage.setItem("Bookrack", JSON.stringify(bookracks));
      } else {
        // 移除书架
        let bookrack = JSON.parse(localStorage.getItem("Bookrack"));
        let isnum = bookrack.findIndex((v) => {
          return (
            v.data.author == this.detailList.author &&
            v.data.name == this.detailList.name
          );
        });
        bookrack.splice(isnum, 1);
        
        localStorage.setItem("Bookrack", JSON.stringify(bookrack));
      }
    },
  },
};
</script>

<style lang="less" scoped>
</style>